<template>
  <v-container>
    <v-card class="transparent" flat>
      <v-card-text>
        <v-card class="my-4 text-center partexam">
          <v-card-text>
            <p>
              [বিঃদ্রঃ এখানে শুধুমাত্র অপার-চিন্ময় ২১ ব্যাচের এক্সামগুলো দেয়া
              যাবে এবং এক্সাম রিলেটেড সকল রিসোর্স পাওয়া যাবে। পাশাপাশি QnA
              সেকশনের মাধ্যমে এখান থেকেই সরাসরি অপার ভাইয়া/চিন্ময় ভাইয়াকে প্রশ্ন
              করা যাবে। আমাদের সকল ক্লাস এবং লেকচার শীট একসাথে পাবার জন্যে এই
              বাটনে ক্লিক করোঃ
            </p>
            <v-btn
              href="https://buet-physics.herokuapp.com"
              target="_blank"
              class="success"
              tile
              large
              >Classes & Lecture Sheets</v-btn
            >
          </v-card-text>
        </v-card>
        <div class="my-3 subject__filters mx-auto">
          <button
            :class="`subject__button ${se == '' && 'activebtn'}`"
            @click="selectSubject('')"
          >
            All
          </button>
          <button
            v-for="(s, i) in subjects"
            :key="i"
            :class="`subject__button ${se == s && 'activebtn'}`"
            @click="selectSubject(s)"
          >
            {{ s }}
          </button>
        </div>
        <v-row>
          <v-col cols="12" v-for="(e, i) in exams" :key="i">
            <v-card class="text-center partexam">
              <v-card
                max-width="600"
                v-if="i == 0"
                class="transparent mx-auto pt-4 px-3"
                flat
              >
                <Timer :end="e.start" v-if="e.start > Date.now()" />
                <h1
                  v-else-if="e.end > Date.now()"
                  class="text-center mx-auto white--text"
                >
                  Ongoing...
                </h1>
              </v-card>
              <v-card-title>
                <h2 class="ma-auto balooda">{{ e.topic }}</h2></v-card-title
              >
              <v-card-text>
                <h2>Starting Time</h2>
                <h3 class="my-2">
                  {{ e.start | moment("MMMM Do YYYY, h:mm:ss a") }}
                </h3>
                <h2>Ending Time</h2>
                <h3 class="mt-2">
                  {{ e.end | moment("MMMM Do YYYY, h:mm:ss a") }}
                </h3>
                <h2 class="white--text mt-2">
                  Per Question marks: 10 <br />
                  Per Question Time: 3 minutes
                </h2>
              </v-card-text>
              <v-card-actions>
                <div class="mx-auto">
                  <v-btn
                    class="success my-2"
                    v-if="e.start < Date.now()"
                    dark
                    large
                    tile
                    :to="`/exam/${e.id}`"
                    >Start Exam</v-btn
                  >
                  <v-btn
                    class="success my-2"
                    v-else
                    @click="upcomingalert"
                    dark
                    large
                    tile
                    >Start Exam</v-btn
                  >
                  <v-btn
                    class="mx-2 success my-2"
                    dark
                    large
                    tile
                    :to="`/ranking/${e.id}`"
                    >Leaderboard</v-btn
                  >
                  <v-btn
                    class="success my-2"
                    dark
                    large
                    tile
                    v-if="e.end < Date.now()"
                    :href="e.solve_sheet"
                    target="_blank"
                    >Solve Sheet</v-btn
                  >
                  <v-btn
                    class="success my-2"
                    dark
                    large
                    tile
                    v-else
                    @click="endalert"
                    >Solve Sheet</v-btn
                  >
                </div>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
        <div v-if="exams.length < 1" class="mt-10 text-center">
          <v-progress-circular
            :size="100"
            :width="10"
            color="success my-2"
            indeterminate
          ></v-progress-circular>
        </div>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
import Timer from "@/components/Timer.vue";
export default {
  components: { Timer },
  data() {
    return {
      alexms: [],
      exams: [],
      subjects: new Set(),
      se: "",
    };
  },
  methods: {
    getRoutineInfo() {
      fetch(
        "https://script.google.com/macros/s/AKfycbxHQvpCJacrv42u8RzaknFKc58rIQldmeY3zUB2EU4eOao0mUVk7ErwPN10r_MJREg4/exec"
      )
        .then((res) => res.json())
        .then((data) => {
          this.alexms = data.exams
            .filter((s) => s.start)
            .map((r) => {
              if (r.topic) {
                let s = r.topic.split(" ")[0];
                this.subjects.add(s);
              }
              return {
                id: r.id.toString(),
                topic: r.topic,
                question: r.question,
                start: new Date(r.start).getTime(),
                end: new Date(r.end).getTime(),
                solve_sheet: r.solve_sheet,
                subject: r.topic.split(" ")[0],
              };
            })
            .sort((a, b) => b.start - a.start);
          this.selectSubject("");
        });
    },
    endalert() {
      this.$swal({
        icon: "warning",
        title: "Will be available after the time ends",
      });
    },
    upcomingalert() {
      this.$swal({
        icon: "warning",
        title: "Upcomming",
        text: "Wait till starting Date.",
      });
    },
    selectSubject(s) {
      this.se = s;
      this.exams = this.alexms.filter((r) => {
        if (r.subject) {
          if (r.subject.toLowerCase().includes(s.toLowerCase())) {
            return r;
          }
        }
      });
    },
  },
  created() {
    this.getRoutineInfo();
  },
};
</script>

<style lang="scss" scoped>
.partexam {
  border-radius: 30px;
  background: #b4b6b398;
  box-shadow: 20px 20px 60px #97ca6e, -20px -20px 60px #cdff94;
}
.subject__button {
  background: rgb(77, 161, 38);
  box-shadow: inset 20px 20px 60px #22a72d, inset -20px -20px 60px #20b35dd3;
  font-weight: 500;
  margin: 5px;
  padding: 10px 15px;
  color: white;
  border-radius: 5px;
  transition: all 0.2s;
  &:hover {
    transform: scale(1.2);
    background: #27a017;
  }
}
.activebtn {
  background: rgba($color: #1f75c5, $alpha: 1);
  box-shadow: inset 20px 20px 60px #396fe4, inset -20px -20px 60px #547ef1;
}
.subject__filters {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-self: center;
}
</style>