<template>
  <div class="timer__container">
    <div>
      <h2>{{ days }}</h2>
      <p>days</p>
    </div>
    <div>
      <h2>{{ hours }}</h2>
      <p>hours</p>
    </div>
    <div>
      <h2>{{ minute }}</h2>
      <p>minute</p>
    </div>
    <div>
      <h2>{{ second }}</h2>
      <p>second</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "timer",
  props: {
    end: {
      required: true,
    },
  },
  data() {
    return {
      days: 0,
      hours: 0,
      minute: 0,
      second: 0,
    };
  },
  methods: {
    showRemaining() {
      const timer = setInterval(() => {
        const distance = this.end - Date.now();

        if (distance < 0) {
          clearInterval(timer);
          return;
        }
        const days = Math.floor(distance / this._days);
        const hours = Math.floor((distance % this._days) / this._hours);
        const minutes = Math.floor((distance % this._hours) / this._minutes);
        const seconds = Math.floor((distance % this._minutes) / this._seconds);
        this.second = seconds < 10 ? "0" + seconds : seconds;
        this.minute = minutes < 10 ? "0" + minutes : minutes;
        this.hours = hours < 10 ? "0" + hours : hours;
        this.days = days < 10 ? "0" + days : days;
      });
    },
  },

  computed: {
    _seconds: () => 1000,
    _minutes() {
      return this._seconds * 60;
    },
    _hours() {
      return this._minutes * 60;
    },
    _days() {
      return this._hours * 24;
    },
  },
  mounted() {
    this.showRemaining();
  },
};
</script>

<style lang="scss" scoped>
.timer__container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 10px;
  justify-content: center;
  text-align: center;
}
.timer__container > div {
  padding-top: 10px;
  border-radius: 5px;
  background: linear-gradient(45deg, #1fa72a, #2fa119);
  color: white;
}
</style>